import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import linkState from 'linkstate';

import InboxUser from '../../components/inbox/users';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(false);
const MARGIN_BOTTOM = mu.marginBottom();

const initialState = {
    isLoading: false,
	limit: 25,
	offset: 0,
    search: '',
};

@observer
class Inbox extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

    async loadUsers({ limit, offset, search }) {
		this.setState({ isLoading: true });
		const { messageStore } = this.props.stores;
		await messageStore.loadUsers({ limit, offset, search });
		this.setState({ isLoading: false });
	}

    searchForUsers = () => {
        const { search, limit } = this.state;
		this.setState({
			offset: 0,
		});
		this.loadUsers({ search, limit });
    }

    componentDidMount() {
		this.loadUsers({});
	}

	// componentWillReceiveProps(nextProps) {
	// 	this.loadMessages(nextProps);
	// }

    render() {
        const { search } = this.state;
        const { messageStore } = this.props.stores;
        const { users } = messageStore;

        return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
                <div class='row fixed-top bg-light' style={`margin-top: ${MARGIN_TOP};`}>
                    <div
                        class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mx-2'
                        // style={`${imageHeader ? `background-image: url("${imageHeader.s3SmallLink}"); background-size: cover;` : ''} background-position: 50% 50%;`}
                    >
                        <a href='/inbox' class='float-left mr-3' style='font-size: 2.8em; line-height: 1.0em;'><i class='fas fa-angle-left' /></a>

                        <div class='d-flex flex-row flex-nowrap position-relative' style='overflow: auto;'>
                            <div 
                                class={`text-center border rounded-circle imageRounded imageRoundedSmall pt-0`}
                                style={`border-width: 3px !important;`}
                            >
                                <span class='font-weight-lighter' style='font-size: 20px;'><i class='fas fa-user' /></span>
                            </div>
                            <div class='flex-grow-1 pl-3 pt-0'>
                                <div class='text-overflow'>
                                    <strong>New message</strong>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div class='row'>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-5'>
                        <div class='d-flex flex-row flex-nowrap'>
                            <div class='flex-grow-1'>
                                <label class='sr-only' for='inboxSearch'><Text id='inbox.search'>Search</Text></label>
                                <Localizer>
                                    <input
                                        type='text'
                                        class='form-control mb-2'
                                        id='inboxSearch'
                                        onInput={linkState(this, 'search')}
                                        value={search}
                                        placeholder={<Text id='inbox.search-for-users'>Search for users you want to send a message to...</Text>}
                                    />
                                </Localizer>
                            </div>
                            <div class='ml-1'>
                                <button
                                    type='button'
                                    class='btn btn-primary mb-2'
                                    onClick={this.searchForUsers}
                                ><Text id='inbox.search-button'>Search</Text></button>
                            </div>
                        </div>
                    </div>
				</div>

                <div class='row'>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-3'>
                        <h5><Text id='inbox.new-message-suggested'>Suggested</Text></h5>
                    </div>
                </div>
                <div class='row'>
                    {users && users.map(user => <>
                        <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-3'>
                            <InboxUser stores={this.props.stores} user={user} showDot={true} />
                        </div>
                    </>)}
                </div>
            </div>
        );
    }
}

export default Inbox;
