import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';

import mu from '../../lib/musher-util';

const initialState = {};

@observer
class InboxUser extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	render() {
        const { user = {}, showDot = false } = this.props;
        const shortname = mu.displayNameShort(user);
        const name = mu.displayName(user, true);
        const userImage = user.image || (user.images ? user.images[0] : undefined) || user.teamImage;
        const teamName = user.teamName;

        const lastMessage = user.lastMessage;
        let isMyMessage;
        let body;
        if (lastMessage) {
            isMyMessage = user.uuidv4 !== lastMessage.updateUser;
            body = lastMessage.body === 'LIKE_MESSAGE' ? <Text id='inbox.like-message'>Liked message</Text> : lastMessage.body;
        }

		return (
            <div class='d-flex flex-row flex-nowrap position-relative' style='overflow: auto;'>
                <div 
                    class={`text-center border rounded-circle imageRounded pt-2`}
                    style={`${userImage ? `background-image: url("${userImage.s3SmallLink}"); background-size: cover;` : ''} border-width: 3px !important;`}
                >
                    {!userImage && <span class='font-weight-lighter' style='font-size: 35px;'>{shortname}</span>}
                </div>
                <div class='flex-grow-1 pl-3 pt-0'>
                    <div class='text-overflow'>
                        <a href={`/inbox/${user.uuidv4}`} class='stretched-link'>
                            <h5>{name}</h5>
                        </a>
                    </div>
                    <div class='text-overflow'>
                        {user.place ? `${user.place}, ` : ''} {user.country}
                    </div>
                    {lastMessage ? <>
                        <div class={`text-overflow`}>
                            {isMyMessage ? <>
                                <span class='font-weight-lighter'><Text id='inbox.you'>You</Text>:</span>
                                <span class={`ml-1 ${lastMessage.readDate ? '' : 'font-weight-bold'}`}>
                                    {mu.maxLength(body, 30)}
                                </span>
                            </> : <>
                                <span class={lastMessage.readDate ? '' : 'font-weight-bold'}>
                                    {mu.maxLength(body, 30)}
                                </span>
                            </>}
                            <span class='text-muted ml-3'>
                                {util.formatDistance(lastMessage.updated, new Date(), { locale: 'no-NB' })}
                            </span>
                        </div>
                    </> : <>
                        {teamName && <div class='text-overflow'>
                            {teamName}
                        </div>}                    
                    </>}
                </div>
                {showDot && <div class='d-flex align-items-center'>
                    <i class='far fa-circle' style='font-size: 2.0em;' />
                </div>}
            </div>
		);
	}
}

export default InboxUser;
